ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(245, 214, 56);
  }

  a{
    text-decoration:inherit;
    color: inherit;
    display: block;
  }
  
  li {
    float: right;
    font-size: 20px;
  }
  li a {
    color: black;
    margin-right: 20px;
    display: block;
    text-align: center;
    padding: 20px 16px;
    text-decoration: none;
  }

  li a:hover{
    background-color: rgb(0, 0, 0);
    color: white;
  }

  .liimmagine >a{
    padding: inherit;
    margin-left:10px;
  }
  .liimmagine a:hover{
    background-color: rgb(245, 214, 56);
  }

  .immagine{
    width: 100px;
    margin-top: 10px
  }